
.rush-wrapper {
  background: #fff;
  box-shadow: 0 1px 0 #eee;
  margin-top: 5px;
  padding: 0 8px 5px 8px;
  text-align: center;
}
.rush-wrapper .rush-clock {
    display: inline-block;
    font-size: 0;
    position: relative;
    top: -10px;
}
.rush-wrapper .rush-clock i {
      font-size: 20px;
      display: inline-block;
      vertical-align: middle;
      line-height: 20px;
}
.rush-wrapper .rush-clock span {
      margin-left: 3px;
      font-size: 16px;
      display: inline-block;
      vertical-align: middle;
      line-height: 16px;
}
.rush-wrapper .rush-content {
    margin-top: 8px;
}
.rush-wrapper .rush-content .rush-item {
      padding: 12px 8px 12px 8px;
      border-top: 1px solid #eee;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      font-size: 0;
      position: relative;
}
.rush-wrapper .rush-content .rush-item .rush-pic {
        -webkit-box-flex: 0;
            -ms-flex: 0 0 137px;
                flex: 0 0 137px;
        width: 137px;
        height: 104px;
        display: inline-block;
}
.rush-wrapper .rush-content .rush-item .rush-desc {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        margin-left: 20px;
}
.rush-wrapper .rush-content .rush-item .rush-desc .rush-item-title {
          font-size: 16px;
          color: #333;
          font-weight: bold;
          text-align: left;
          margin-top: 5px;
}
.rush-wrapper .rush-content .rush-item .rush-btn {
        position: absolute;
        bottom: 12px;
        right: 8px;
}
.rush-wrapper .rush-content .rush-item .rush-detail {
        font-size: 12px;
        line-height: 16px;
        color: #999;
        text-align: left;
        margin-top: 6px;
        display: -webkit-box !important;
        overflow: hidden;
        text-overflow: ellipsis;
        word-break: break-all;
        box-orient: vertical;
        line-clamp: 3;
}
.rush-wrapper .rush-content .rush-item .rush-price {
        font-size: 16px;
        color: #ff0000;
        margin-top: 11px;
        text-align: left;
        font-weight: bold;
}



