
.pro-box {
  max-width: 640px;
  margin: 0 auto;
  overflow: hidden;
}
.pro-box .xm-list {
    padding: 6px 8px;
    background: #fff;
    margin-top: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    box-shadow: 0 1px 0 #eee;
}
.pro-box .xm-list .xm-pic {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 130px;
              flex: 0 0 130px;
      width: 130px;
}
.pro-box .xm-list .xm-content {
      margin-left: 12px;
}
.pro-box .xm-list .xm-content .xm-title {
        font-size: 14px;
        color: #000;
        font-weight: 700;
        margin-top: 5px;
}
.pro-box .xm-list .xm-content .xm-desc {
        font-size: 12px;
        line-height: 16px;
        color: #999;
        margin: 8px 0;
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        word-break: break-all;
        box-orient: vertical;
        line-clamp: 4;
}
.pro-box .xm-list .xm-content .xm-tags > span {
        display: inline-block;
        width: 53px;
        height: 16px;
        color: #08c5bf;
        border-radius: 5px;
        border: 1px solid #08c5bf;
        font-size: 9px;
        line-height: 16px;
        text-align: center;
        margin-right: 3px;
}
.pro-box .xm-list .xm-content .xm-tags > span > i {
          font-size: 9px;
          margin-right: 2px;
          line-height: 16px;
}
.pro-box .xm-listcontent {
    background: #fff;
    padding: 0 8px;
    margin-top: 8px;
}
.pro-box .xm-listcontent .xm-people {
      font-size: 0;
      padding-top: 10px;
      color: #000;
      border-bottom: 1px solid #eee;
}
.pro-box .xm-listcontent .xm-people > span {
        margin-left: 3px;
        font-size: 16px;
        line-height: 1.2;
        display: inline-block;
        vertical-align: middle;
}
.pro-box .xm-listcontent .xm-people i {
        font-size: 28px;
        line-height: 1.2;
        display: inline-block;
        vertical-align: middle;
}
.pro-box .xm-listcontent .people-content {
      font-size: 12px;
      color: #333;
      margin-top: 10px;
      line-height: 16px;
}
.pro-box .xm-listcontent .xm-advantage i {
      font-size: 20px;
}
.pro-box .xm-listcontent .xm-compare-pic {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      margin-top: 10px;
}
.pro-box .xm-listcontent .xm-compare-pic .pic-before {
        margin-right: 8px;
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
}
.pro-box .xm-listcontent .xm-compare-pic .pic-after {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
}
.pro-box .xm-listcontent .docotor-detail {
      margin-top: 10px;
}



