
.footer {
  max-width: 640px;
  margin: 0 auto;
  overflow: hidden;
}
.footer .tel-wrapper {
    padding: 5px 8px 15px 8px;
}
.footer .address {
    position: relative;
}
.footer .address .my-info {
      position: absolute;
      font-size: 0;
      right: 5px;
      top: 30px;
}
.footer .address .my-info i {
        display: inline-block;
        width: 23px;
        height: 23px;
        font-size: 24px;
        color: #000;
        font-weight: 900;
        vertical-align: top;
        text-align: center;
}
.footer .address .my-info span {
        display: inline-block;
        vertical-align: top;
        font-size: 12px;
        line-height: 16px;
        margin-left: 2px;
}
.footer .address .my-info .my-address {
        margin-top: 10px;
}
.footer .address .my-info .my-map {
        width: 75px;
        height: 23px;
        background: #333;
        border-radius: 10px;
        color: #fff;
        font-size: 12px;
        text-align: center;
        line-height: 24px;
        margin: 5px 0 0 25px;
}
.footer .copyright {
    position: absolute;
    bottom: 5px;
    left: 0;
    width: 100%;
    text-align: center;
    font-size: 9px;
    line-height: 12px;
    color: #c2c2c2;
}



